import React from "react";

//STYLE
import {
  BodyH1,
  BodyH2,
  BodyH3,
  BodyH4,
  BodyH5,
} from "../../../theme/GlobalStyles";
import DetailWrapper, { Icon } from "./transactiondetail.style";
import { FiAlignLeft, FiGrid, FiRepeat, FiMinus, FiPlus } from "react-icons/fi";
import {
  HiOutlineQrcode,
  HiOutlineMinusCircle,
  HiOutlinePlusCircle,
  HiOutlineClock,
  HiOutlineCheckCircle,
  HiOutlineXCircle,
  HiOutlineEye,
  HiOutlinePencilAlt,
  HiOutlineTrash,
} from "react-icons/hi";
import { useTheme } from "../../../theme/useTheme";
import { Link } from "react-router-dom";
import GoBack from "../../../componants/GoBack";

const TransactionDeatil = (props) => {
  const { theme, themeLoaded } = useTheme();
  const dataItem = (props.location && props.location.state) || {};
  const [locData, setlocData] = React.useState(dataItem);
  React.useEffect(() => {
    setlocData(dataItem.length);
  }, [locData]);
  const renderStatus = (status, iconSize, iconColor) => {
    if (status == "All") {
      return null;
    }
    if (status == "Pending") {
      return <HiOutlineClock style={{ color: theme.colors.yellow }} />;
    }
    if (status == "Complete") {
      return <HiOutlineCheckCircle style={{ color: theme.colors.green }} />;
    }
    if (status == "Cancelled") {
      return <HiOutlineXCircle style={{ color: theme.colors.red }} />;
    }
    if (status == "Overdue") {
      return <HiOutlineXCircle style={{ color: theme.colors.gray }} />;
    }
  };
  return (
    <DetailWrapper>
      <div className="top-sec">
        <div className="cal-cont">
          <div className="btc-tip">
            <div className="a">
              <div className="box">
                <div className="label">BTC</div>
                <div className="btc-tip-amount">0.01539</div>
              </div>
            </div>
            <div className="">
              <div className="box">+</div>
            </div>
            <div className="a">
              <div className="box">
                <div className="label">Tip</div>
                <div className="btc-tip-amount">0.01539</div>
              </div>
            </div>
          </div>
          <div className="btc-total">
            <div className="box">
              <div className="label">Total</div>
              <div className="total-amount">0.01539</div>
            </div>
          </div>
        </div>
      </div>
      {Object.keys(dataItem).length === 0 ? (
        <div>
          <GoBack props={props} />
        </div>
      ) : (
        <div className="bottom-sec">
          <div className="bottom-sec-inner">
            <div className="user-info">
              <div className="user-detail">
                <div className="user-img">
                  <img src={dataItem.picture.medium} alt="" />
                </div>
                <div className="user-name">
                  <BodyH1>
                    {dataItem.name.first}
                    {dataItem.name.last}
                  </BodyH1>
                  <BodyH3>Memo</BodyH3>
                </div>
              </div>
              <div className="status">
                <Icon>{renderStatus(dataItem.status)}</Icon>
                <BodyH5>{dataItem.status}</BodyH5>
              </div>
            </div>

            <div className="tr-info">
              <div className="left">
                <Icon>
                  <FiRepeat />
                </Icon>
                <BodyH3>{dataItem.dob}</BodyH3>
              </div>
              <div>
                <Icon>
                  <FiRepeat />
                </Icon>
              </div>
            </div>
            <div className="tr-info">
              <div className="left">
                <Icon>
                  <FiRepeat />
                </Icon>
                <BodyH3>{dataItem.dob}</BodyH3>
              </div>
              <div>
                <Icon>{/* <FiRepeat /> */}</Icon>
              </div>
            </div>
            <div className="tr-info">
              <div className="left">
                <Icon>
                  <FiRepeat />
                </Icon>
                <BodyH3>{dataItem.email}</BodyH3>
              </div>
              <div>
                <Icon>{/* <FiRepeat /> */}</Icon>
              </div>
            </div>
            <div className="tr-info">
              <div className="left">
                <Icon>
                  <FiRepeat />
                </Icon>
                <BodyH3>{dataItem.gender}</BodyH3>
              </div>
              <div>
                <Icon>{/* <FiRepeat /> */}</Icon>
              </div>
            </div>
            <div className="tr-info">
              <div className="left">
                <Icon>
                  <FiRepeat />
                </Icon>
                <BodyH3>{dataItem.email}</BodyH3>
              </div>
              <div>
                <Icon>
                  <FiRepeat />
                </Icon>
              </div>
            </div>
            <div className="tr-info">
              <div className="left">
                <Icon>
                  <FiRepeat />
                </Icon>
                <BodyH3>{dataItem.email}</BodyH3>
              </div>
              <div>
                <Icon>
                  <FiRepeat />
                </Icon>
              </div>
            </div>
            <div className="tr-info">
              <div className="left">
                <Icon>
                  <FiRepeat />
                </Icon>
                <div>
                  <BodyH3>{dataItem.registered}</BodyH3>
                </div>
              </div>
              <div>
                <Icon>
                  <FiRepeat />
                </Icon>
              </div>
            </div>
          </div>
        </div>
      )}
    </DetailWrapper>
  );
};
export default TransactionDeatil;
